/* eslint-disable no-nested-ternary */
/* eslint-disable id-length */
/* eslint-disable react/button-has-type */
/* eslint-disable object-curly-newline */
/* eslint-disable arrow-parens */

import React, { useState } from 'react';
import { Checkbox } from 'antd';
import {
  TicketsArea,
  Button,
  ButtonArea,
  PriceArea,
  DescriptionArea,
  FirstPoint,
  SecondPoint,
} from './styles';
import { CheckboxArea, CheckboxItem, CheckboxSpan, CheckText } from './checkbox';
import classes from './App.module.css';

const AviaSales = props => {
  const [step, setStep] = useState({
    step: 1,
  });

  const [checked, setChecked] = useState([]);

  let { ticketsList } = props;
  const checkbox = [
    { key: 1, id: 1, value: 'Без остановок', steps: 0 },
    { key: 2, id: 2, value: '1 остановка', steps: 1 },
    { key: 3, id: 3, value: '2 остановки', steps: 2 },
    { key: 4, id: 4, value: '3 остановки', steps: 3 },
    { key: 5, id: 5, value: 'Все', steps: 5 },
  ];
  const [data, setData] = useState([]);
  const handler = checked;
  let newChecked = [];
  const changing = num => {
    ticketsList = props.mainTickets;
    setData([]);
    const currentIndex = checked.indexOf(num);
    newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(num);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
    const sorted = newChecked.sort((a, b) => b - a);
    if (sorted.length === 0) {
      sorted[0] = 1;
    }
    setStep({
      ...step,
      step: sorted[0] - 1,
    });
  };

  const number = step.step;
  let renderingData = [];
  const cheap = () => {
    setStep({
      ...step,
      step: number,
    });
    let cheapData = [...data];
    cheapData = renderingData.sort((a, b) => a.price - b.price);
    setData(cheapData);
  };
  const quick = () => {
    setStep({
      ...step,
      step: number,
    });
    let quickData = [...data];
    quickData = renderingData.sort(
      (a, b) => a.timeForward + a.timeToward - (b.timeForward + b.timeToward)
    );
    setData(quickData);
  };

  const arr = [];
  if (props.mainTickets.length !== 0) {
    ticketsList = props.mainTickets;
  }

  for (let i = 0; i < ticketsList.length; i++) {
    const obj = {
      id: i,
      price: ticketsList[i].price,
      company: `//pics.avs.io/99/36/{${ticketsList[i].carrier}}.png`,
      forward: `${ticketsList[i].segments[0].origin}-${ticketsList[i].segments[0].destination}`,
      toward: `${ticketsList[i].segments[1].origin}-${ticketsList[i].segments[0].destination}`,
      timeForward: ticketsList[i].segments[0].duration,
      timeToward: ticketsList[i].segments[1].duration,
      stepsForward: ticketsList[i].segments[0].stops.length,
      stepsToward: ticketsList[i].segments[1].stops.length,
      stepsForwardCities: ticketsList[i].segments[0].stops.join('-'),
      stepsTowardCities: ticketsList[i].segments[1].stops.join('-'),
      filter: Number(
        ticketsList[i].segments[0].stops.length + ticketsList[i].segments[1].stops.length
      ),
      data: ticketsList[i].segments[0].date
        .split('T')[1]
        .split('.')[0]
        .split(':')
        .splice(0, 2),
    };
    arr.push(obj);
  }
  if (arr.length > 0) {
    console.log(arr[0].data);
  }
  let filter = arr.filter(e => e.stepsForward + e.stepsToward === number);

  if (handler.length > 1) {
    const firstPart = arr.filter(item => item.filter === handler[0] - 1).slice(0, 3);
    const secondPart = arr
      .filter(item => item.filter === handler[handler.length - 1] - 1)
      .slice(0, 2)
      .concat(firstPart);
    filter = secondPart;
  }

  if (handler.length > 2) {
    const firstPart = arr.filter(item => item.filter === handler[0] - 1).slice(0, 2);
    const secondPart = arr
      .filter(item => item.filter === handler[handler.length - 1] - 1)
      .slice(0, 1)
      .concat(firstPart);
    const thirdPart = arr
      .filter(item => item.filter === handler[handler.length - 2] - 1)
      .slice(0, 2)
      .concat(secondPart);
    filter = thirdPart;
  }

  if (number > 3) {
    filter = arr.filter(e => e.stepsForward + e.stepsToward <= number);
  }

  for (let i = 0; i < 5; i++) {
    renderingData.push(filter[i]);
  }

  if (data.length > 0) {
    renderingData = data;
  }
  const mappedTickets = renderingData.filter(e => e !== undefined);

  if (arr.length !== 0) {
    return (
      <div className={classes.body}>
        <CheckboxArea>
          <CheckText>Количество остановок</CheckText>
          {checkbox.map(el => (
            <CheckboxItem key={el.key}>
              <CheckboxSpan>
                <Checkbox onClick={() => changing(el.id)} checked={checked.indexOf(el.id) !== -1}>
                  {el.value}
                </Checkbox>
              </CheckboxSpan>
            </CheckboxItem>
          ))}
        </CheckboxArea>
        <div className={classes.wrapper}>
          <ButtonArea>
            <Button onClick={cheap}>
              <p>Самый дешевый</p>
            </Button>
            <Button onClick={quick}>
              <p>Самый быстрый</p>
            </Button>
          </ButtonArea>
          {mappedTickets.map(el => (
            <TicketsArea key={el.id}>
              <PriceArea>
                <p>{`${el.price} P`}</p>
                <img src={el.company} alt="" />
              </PriceArea>
              <DescriptionArea>
                <FirstPoint>
                  <ul>
                    <li>
                      <span>{el.forward}</span>
                    </li>
                    <li>
                      {`${el.data[0]}:${el.data[1]} - ${
                        Number(el.data[0]) + Number(Math.floor(el.timeForward / 60)) > 24
                          ? Number(el.data[0]) + Number(Math.floor(el.timeForward / 60)) - 24
                          : Number(el.timeForward - Math.floor(el.timeForward / 60) * 60) + Number(el.data[1]) >= 60 
                            ? Number(el.data[0]) + Number(Math.floor(el.timeForward / 60)) + 1 
                            : Number(el.data[0]) + Number(Math.floor(el.timeForward / 60)) 
                      }: ${
                        Number(el.timeForward - Math.floor(el.timeForward / 60) * 60) + Number(el.data[1]) >= 60
                          ? Number(el.timeForward - Math.floor(el.timeForward / 60) * 60) + Number(el.data[1]) - 60
                          : Number(el.timeForward - Math.floor(el.timeForward / 60) * 60) + Number(el.data[1]) < 10
                            ? String(`${Number(el.data[0]) - Number(el.data[0])}
                          ${Number(el.timeForward - Math.floor(el.timeForward / 60) * 60) + Number(el.data[1])}`)
                            : Number(el.timeForward - Math.floor(el.timeForward / 60) * 60) + Number(el.data[1])
                      }`}
                    </li>
                  </ul>
                  <ul>
                    <li>
                      <span>в пути</span>
                    </li>
                    <li>{`${Math.floor(el.timeForward / 60)} h ${el.timeForward - Math.floor(el.timeForward / 60) * 60} min`}</li>
                  </ul>
                  <ul>
                    <li>
                      <span>Пересадки</span>
                    </li>
                    <li>{el.stepsForwardCities.length > 0 ? el.stepsForwardCities : 'Нет'}</li>
                  </ul>
                </FirstPoint>
                <SecondPoint>
                  <ul>
                    <li>
                      <span>{el.toward}</span>
                    </li>
                    <li>
                    {`${el.data[0]}:${el.data[1]} - ${
                        Number(el.data[0]) + Number(Math.floor(el.timeToward / 60)) > 24
                          ? Number(el.data[0]) + Number(Math.floor(el.timeToward / 60)) - 24
                          : Number(el.timeToward - Math.floor(el.timeToward / 60) * 60) + Number(el.data[1]) >= 60 
                            ? Number(el.data[0]) + Number(Math.floor(el.timeToward / 60)) + 1 
                            : Number(el.data[0]) + Number(Math.floor(el.timeToward / 60)) 
                      }: ${
                        Number(el.timeToward - Math.floor(el.timeToward / 60) * 60) + Number(el.data[1]) >= 60
                          ? Number(el.timeToward - Math.floor(el.timeToward / 60) * 60) + Number(el.data[1]) - 60
                          : Number(el.timeToward - Math.floor(el.timeToward / 60) * 60) + Number(el.data[1]) < 10
                            ? String(`${Number(el.data[0]) - Number(el.data[0])}
                          ${Number(el.timeToward - Math.floor(el.timeToward / 60) * 60) + Number(el.data[1])}`)
                            : Number(el.timeToward - Math.floor(el.timeToward / 60) * 60) + Number(el.data[1])
                      }`}
                    </li>
                  </ul>
                  <ul>
                    <li>
                      <span>в пути</span>
                    </li>
                    <li>{`${Math.floor(el.timeToward / 60)} h ${el.timeToward - Math.floor(el.timeToward / 60) * 60} min`}</li>
                  </ul>
                  <ul>
                    <li>
                      <span>Пересадки</span>
                    </li>
                    <li>{el.stepsTowardCities.length > 0 ? el.stepsTowardCities : 'Нет'}</li>
                  </ul>
                </SecondPoint>
              </DescriptionArea>
            </TicketsArea>
          ))}
        </div>
      </div>
    );
  }
  return 'please wait';
};
export default AviaSales;
